import React from "react";
import { Outlet,useNavigate } from "react-router-dom";
import Avatar from "@/assets/img/home/avatar.png";

import "@/style/home/home.scss";
function HomeNavBar() {
  const navigate = useNavigate();
  // 
  const GotoArchives = () => {
    navigate("/archives");
  };
  return (
    <div>
      {/*  */}
      <div className="header_inner">
        {/*  */}
        <div className="avatar">
          <img src={Avatar} alt="" />
        </div>
        {/*  */}
        <div>
          <span>
            <i className="iconfont icon-home"></i>
          </span>
          <span 
          onClick={() => GotoArchives()}
          >
            <i className="iconfont icon-dingdan"></i>
          </span>
          <span>
            <i className="iconfont icon-qingdan"></i>
          </span>
          <span>
            <i className="iconfont icon-xinqing-xian"></i>
          </span>
          <span>
            <i className="iconfont icon-zhaoxiangji"></i>
          </span>
          <span>
            <i className="iconfont icon-liuyanban-xian"></i>
          </span>
          <span>
            <i className="iconfont icon-lianjie"></i>
          </span>
          <span>
            <i className="iconfont icon-xihuan"></i>
          </span>
          <span>
            <i className="iconfont icon-Tree"></i>
          </span>
          <span>
            <i className="iconfont icon-home"></i>
          </span>
        </div>
        {/*  */}
        <div>
          <span>
            <i className="iconfont icon-search"></i>
          </span>
          <span>
            <i className="iconfont icon-user"></i>
          </span>
        </div>
      </div>
      <Outlet />
    </div>
  );
}

export default HomeNavBar;
